import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import twitterLogo from '../assets/static/twitter-color.png';
import googleLogo from '../assets/static/google-color.png';
import '../assets/styles/components/LoginUser.scss';
import { loginUser } from '../actions';

const Login = (props) => {
  const [form, setValues] = useState({
    email: '',
  });

  const handleInput = (event) => {
    setValues({
      ...form,
      [event.target.name]: event.target.value,
    });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    props.loginUser(form, '/main');
  };

  return (
    <section className='login'>
      <section className='login__container'>
        <h2>Inicia sesin</h2>
        <form className='login__container--form' onSubmit={handleSubmit}>
          <input
            name='email'
            className='input'
            type='text'
            placeholder='Correo'
            onChange={handleInput}
            required
          />
          <input
            name='password'
            className='input'
            type='password'
            placeholder='Contrasea'
            onChange={handleInput}
            required
          />
          <button type='submit' className='login__container--button'>Iniciar sesin</button>
          <div className='login__container--remember-me'>
            {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
            <label>
              <input type='checkbox' id='cbox1' value='first_checkbox' />
              {'  '}
              Recurdame
            </label>
            <a href='/'>Olvid mi contrasea</a>
          </div>
        </form>
        <section className='login__container--social-media'>
          <div>
            <img src={googleLogo} alt='Inicia sesin con Google' />
            Inicia sesin con Google
          </div>
          <div>
            <img src={twitterLogo} alt='Inicia sesin con Twitter' />
            Inicia sesin con Twitter
          </div>
        </section>
        <p className='login__container--register'>
          No tienes ninguna cuenta
          { ' ' }
          <Link to='/register'>Regstrate</Link>
        </p>
      </section>
    </section>
  );
};

const mapDispatchToProps = {
  loginUser,
};

export default connect(null, mapDispatchToProps)(Login);
